/* stylelint-disable */

@mixin tablet() {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (480px)) {
    @content;
  }
}

@mixin small-mobile {
  @media (max-width: (360px)) {
    @content;
  }
}
